.main-nav__menu-first {
  padding: 0;
}

.main-nav__logo {
  display: block;
  padding-top: 22px;
  padding-bottom: 16px;
  text-align: center;
  border-bottom: 1px solid $color-light-grey;

  &[href]:hover .main-nav__logo-image,
  &[href]:focus .main-nav__logo-image {
    opacity: 0.6;
  }

  &[href]:active .main-nav__logo-image {
    opacity: 0.3;
  }
}

.main-nav__toggle {
  position: absolute;
  top: 24px;
  right: 21px;
  width: 40px;
  height: 30px;
  background: url("../img/icon-menu-close.svg") no-repeat 50% 50%;
  border: none;
}

.main-nav__toggle--open {
  background: url("../img/icon-menu-open.svg") no-repeat 50% 50%;
}

.main-nav__list {
  @include reset-list;
  display: flex;
  flex-direction: column;
  font-size: 17px;
  font-weight: $font-weight-bold;
}

.main-nav__link--loupe {
  position: relative;
}

.main-nav__icon {
  position: absolute;
}

.main-nav__icon--loupe {
  top: 31px;
  left: 32px;
}

.main-nav__link--basket {
  position: relative;
}

.main-nav__icon--basket {
  top: 29px;
  left: 30px;
}

.main-nav__list--other,
.main-nav__list--delivery {
  display: none;
}

.main-nav__link {
  display: block;
  padding: 27px 30px 27px 75px;
  color: $color-black;

  &[href]:hover,
  &[href]:focus {
    color: rgba($color-black, 0.6);
  }

  &[href]:active {
    color: rgba($color-black, 0.3);
  }

  &:hover .main-nav__icon,
  &:focus .main-nav__icon {
    fill: #63d1bb;
  }

  &:active .main-nav__icon {
    opacity: 0.3;
  }
}

.main-nav__item {
  border-bottom: 1px solid $color-light-grey;
}

.main-nav__list--hide {
  display: none;
}

.main-nav__list--show {
  display: block;
}

@media (min-width: $tablet-width) {
  .main-nav__list--hide {
    display: flex;
  }

  .main-nav__menu-first,
  .main-nav__menu-second {
    display: flex;
    justify-content: space-between;
  }

  .main-nav__wrapper-first {
    border-bottom: 1px solid $color-light-grey;
  }

  .main-nav__wrapper-second {
    background-color: #f9f9f9;
  }

  .main-nav__wrapper-second--color {
    background-color: $color-light-grey-2;
  }

  .main-nav__menu-first {
    position: relative;
    padding: 0 50px;
  }

  .main-nav__list {
    flex-direction: row;
  }

  .main-nav__item {
    border: none;
  }

  .main-nav__logo {
    padding-bottom: 14px;
    border: none;
  }

  .main-nav__toggle {
    display: none;
  }

  .main-nav__list--box {
    position: absolute;
    left: 50px;
    bottom: -42px;
    z-index: 1;
  }

  .main-nav__link--catalog {
    padding: 0;
    padding-right: 36px;
  }

  .main-nav__link--order {
    padding: 0;
  }

  .main-nav__link--loupe {
    padding: 50px 31px 14px 51px;
    font-size: 0;
  }

  .main-nav__icon--loupe {
    top: 36px;
    left: 33px;
  }

  .main-nav__link--basket {
    padding: 31px 0 33px 83px;
  }

  .main-nav__icon--basket {
    top: 34px;
    left: 31px;
  }

  .main-nav__list--box,
  .main-nav__list--delivery {
    font-size: 15px;
  }

  .main-nav__item--loupe {
    border-right: 1px solid $color-light-grey;
    border-left: 1px solid $color-light-grey;
  }

  .main-nav__list--delivery {
    display: flex;
    flex-wrap: wrap;
    max-width: 305px;
    padding-top: 18px;
    margin-left: auto;
    font-weight: 400;
  }

  .main-nav__item--delivery:not(:last-child) {
    padding-right: 20px;
  }
}

@media (min-width: $desktop-width) {
  .main-nav {
    position: relative;
  }

  .main-nav__logo {
    padding: 0;
    padding-top: 7px;
    padding-right: 26px;
  }

  .main-nav__list--box {
    position: static;
    order: -1;
    max-width: 380px;
    font-size: 17px;
    background-color: transparent;
    border-top: none;
  }

  .main-nav__item--user {
    border-right: 1px solid $color-light-grey;
  }

  .main-nav__link--catalog {
    padding-top: 31px;
    padding-bottom: 33px;
  }

  .main-nav__list--user {
    max-width: 350px;
  }

  .main-nav__link--order {
    padding-top: 31px;
    padding-left: 34px;
    padding-bottom: 33px;
  }

  .main-nav__list--other {
    display: flex;
    flex-wrap: wrap;
    font-size: 15px;
    max-width: 500px;
  }

  .main-nav__list--delivery {
    max-width: 500px;
    margin-left: 0;
  }

  .main-nav__link--new,
  .main-nav__link--sale {
    padding: 18px 0 0;
  }

  .main-nav__item--other:not(:last-child) {
    padding-right: 62px;
  }
}
