.contacts {
  position: relative;
  padding: 0;
  padding-top: 35px;
  margin-bottom: 40px;

  &::after {
    position: absolute;
    content: "";
    top: -5px;
    left: 0;
    right: 0;
    width: 100%;
    height: 6px;
    margin: 0 auto;
    background: url("../img/bg-zigzag-line.svg") repeat-x;
    background-size: contain;
  }
}

.contacts__wrapper {
  padding-bottom: 35px;
}

.contacts__icon-flag {
  display: none;
}

.contacts__title {
  @include reset-indent;
  font-size: 18px;
  line-height: 30px;
  padding-bottom: 30px;
}

.contacts__item {
  display: flex;
  // width: 100%;
}

.contacts__email,
.contacts__adress,
.contacts__email-name,
.contacts__adress-text {
  @include reset-indent;
}

.contacts__item:not(:last-child) {
  padding-bottom: 35px;
}

.contacts__email,
.contacts__adress {
  margin-right: 32px;
}

.contacts__adress-text {
  line-height: 30px;
}

.contacts__email-link {
  color: $color-dark-grey;
  padding-bottom: 3px;
  border-bottom: 1px solid $color-mint;

  &:hover {
    color: $color-mint;
  }

  &:active {
    opacity: 0.3;
  }
}

.contacts__map {
  position: relative;
  margin-bottom: 41px;
  text-align: center;
}

.contacts__map-iframe {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

.contacts__map-image {
  display: block;
  width: 100%;
}

.contacts__btn {
  margin-bottom: 10px;
}

@media (min-width: 360px) and (max-width: 767px), (min-width: 820px) and (max-width: 1149px) {
  .contacts__adress-text br {
    display: none;
  }
}

@media (min-width: $tablet-width) {
  .contacts {
    position: relative;
    padding-top: 62px;
    margin-bottom: 57px;

    &::before {
      position: absolute;
      content: "";
      top: 47px;
      right: 53px;
      width: 47px;
      height: 82px;
      background: url("../img/icon-flag.svg") no-repeat;
    }

    &::after {
      top: -15px;
      height: 15px;
    }
  }

  .contacts__wrapper {
    padding-bottom: 53px;
  }

  .contacts__title {
    font-size: 43px;
    line-height: 50px;
    padding-bottom: 52px;
  }

  .contacts__wrapper-info {
    display: flex;
  }

  .contacts__item {
    padding-bottom: 0;
    font-size: 17px;

    &:not(:last-child) {
      margin-right: 50px;
    }
  }

  .contacts__email {
    margin-right: 24px;
  }

  .contacts__adress {
    margin-right: 25px;
  }

  .contacts__map {
    margin-bottom: 55px;
  }

  .contacts__wrapper-btn {
    width: 30%;
    margin: 0 auto;
    padding: 0;
  }
}

@media (min-width: $desktop-width) {
  .contacts {
    position: relative;
    padding-top: 141px;
    padding-left: 225px;
    margin-bottom: 104px;

    &::before {
      top: 133px;
      left: 113px;
    }

    &::after {
      top: -23px;
      width: 90%;
      height: 23px;
    }
  }

  .contacts__title {
    font-size: 53px;
    line-height: 57px;
  }

  .contacts__wrapper {
    padding: 0;
    padding-bottom: 77px;
  }

  .contacts__wrapper-info {
    flex-direction: column;
    max-width: 42%;
  }

  .contacts__map {
    position: absolute;
    top: 71px;
    right: 51px;
  }

  .contacts__wrapper-btn {
    width: 25%;
    margin: 0;
  }
}
