.page-footer {
  background-color: $color-light-grey-2;
}

.page-footer__social {
  padding: 26px 0 19px;
  border-bottom: 1px solid #e5e5e5;
}

.page-footer__copyright {
  padding: 23px 0 17px;
}

.page-footer__logo {
  display: none;
}

@media (min-width: $tablet-width) {
  .page-footer {
    padding-top: 50px;
    padding-bottom: 22px;
  }

  .page-footer__wrapper {
    display: flex;
    justify-content: space-between;
  }

  .page-footer__logo {
    display: block;
    padding-right: 82px;

    &[href]:hover .page-footer__logo-image,
    &[href]:focus .page-footer__logo-image {
      fill: #46c1ae;
    }

    &[href]:active .page-footer__logo-image {
      opacity: 0.3;
    }
  }

  .page-footer__social {
    max-width: 45%;
    padding: 8px 0 29px;
    border-bottom: none;
  }

  .page-footer__copyright {
    padding-top: 8px;
  }
}

@media (min-width: $desktop-width) {
  .page-footer__social {
    max-width: 65%;
  }

  .page-footer__copyright {
    padding-top: 8px;
  }
}
