.product {
  position: relative;
  padding-top: 27px;
  padding-bottom: 30px;
}

.product__title {
  margin: 0;
  padding-bottom: 5px;
  font-size: 25px;
  line-height: 30px;
}

.product__week {
  margin: 0;
  padding-bottom: 23px;
}

.product__text-wrapper {
  margin-bottom: 27px;
}

.product__text {
  @include reset-indent;
  line-height: 24px;
  padding-bottom: 16px;
}

.product__table {
  @include reset-indent;
  width: 100%;
  border-collapse: collapse;
}

.product__row {
  border-bottom: 1px solid $color-light-grey;
  padding-bottom: 4px;
}

.product__table-field {
  padding: 0 12px 0 0;
  font-weight: 400;
  line-height: 38px;
  text-align: left;
}

.product__table-value {
  line-height: 38px;
}

.product__image-wrapper {
  padding-bottom: 19px;
  text-align: center;
}

.product__image {
  width: 100%;
}

.product__price {
  @include reset-indent;
  font-size: 17px;
  line-height: 24px;
  font-weight: $font-weight-bold;
  text-align: center;
  padding-bottom: 26px;
}

.product__btn {
  margin-bottom: 9px;
}

@media (min-width: $tablet-width) {
  .product {
    padding-top: 45px;
  }

  .product__title-wrapper {
    display: flex;
    justify-content: space-between;
    padding-bottom: 51px;
  }

  .product__title {
    padding-bottom: 0;
    font-size: 43px;
    line-height: 50px;
  }

  .product__week {
    max-width: 73px;
    padding-right: 26px;
    padding-top: 12px;
    padding-bottom: 0;
    line-height: 17px;
    text-align: center;
    box-sizing: content-box;
  }

  .product__wrapper {
    max-width: 293px;
  }

  .product__text-wrapper {
    margin-bottom: 53px;
  }

  .product__text {
    padding-bottom: 34px;
    font-size: 17px;
    line-height: 30px;
  }

  .product__table-field {
    @include reset-indent;
  }

  .product__image-wrapper {
    position: absolute;
    top: 153px;
    right: 51px;
    padding-bottom: 0;
  }

  .product__price-wrapper {
    bottom: 16px;
    padding-bottom: 30px;
  }

  .product__price {
    font-size: 20px;
    padding-bottom: 35px;
  }
}

@media (min-width: $desktop-width) {
  .product {
    padding-top: 123px;
    padding-bottom: 57px;
  }

  .product__title-wrapper {
    flex-direction: row-reverse;
    justify-content: flex-end;
  }

  .product__title {
    padding-bottom: 0;
    font-size: 53px;
    line-height: 57px;
  }

  .product__week {
    padding: 15px 53px 0 46px;
  }

  .product__wrapper {
    margin-left: 175px;
  }

  .product__text-wrapper {
    margin-bottom: 107px;
  }

  .product__text {
    padding-bottom: 45px;
  }

  .product__image-wrapper {
    top: 71px;
    right: 50px;
    z-index: -1;
  }
}
