.title {
  font-weight: $font-weight-bold;
  font-size: 25px;
  line-height: 30px;
  color: $color-black;
}

@media (min-width: $tablet-width) {
  .title {
    font-size: 43px;
    line-height: 50px;
  }
}

@media (min-width: $desktop-width) {
  .title {
    font-size: 53px;
    line-height: 57px;
  }
}
